import { faCartPlus, faMinusCircle } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import React, { useState } from "react";
import Gallery from "react-grid-gallery";

export default function ArtByArtist({
  artist,
  art,
  artCart,
  addArtToCart,
  removeArtFromCart,
}) {
  const [image, setImage] = useState({ artist: artist, price: 0, _id: "" });

  const onCurrentImageChange = (index) => {
    setImage(art[index]);
  };
  return (
    <div>
      <div className="container">
        <h3>Artist: {artist}</h3>
        <div
          className=""
          style={{
            display: "block",
            width: "100%",
            overflow: "auto",
          }}
        >
          <Gallery
            images={art.filter((x) => x.artist === artist)}
            enableLightbox={true}
            enableImageSelection={false}
            currentImageWillChange={onCurrentImageChange}
            customControls={[
              <div key={image._id}>
                {!image.isSelected? <button
                  type="button"
                  key="addToCart"
                  className="btn btn-primary rounded-pill"
                  onClick={() => addArtToCart(image)}
                >
                  <FontAwesomeIcon icon={faCartPlus} /> {image.artist} |{" "}
                  {Intl.NumberFormat("de-DE", {
                    style: "currency",
                    currency: "EUR",
                  }).format(image.price)}
                </button> :
                <button
                  type="button"
                  className="btn btn-danger rounded-pill"
                  onClick={() => removeArtFromCart(image)}
                >
                  <FontAwesomeIcon icon={faMinusCircle} /> {image.artist} |{" "}
                  {Intl.NumberFormat("de-DE", {
                    style: "currency",
                    currency: "EUR",
                  }).format(image.price)}
                </button>}
              </div>,
            ]}
          />
        </div>
      </div>
      <hr />
    </div>
  );
}
