import { faCartPlus, faMinus, faPlus } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import React, { useState } from "react";
import { Card, Modal, OverlayTrigger, Tooltip } from "react-bootstrap";
import BannarText from "./BannarText";

export default function WineCellar({
  wines,
  addWineToCart,
  removeWineFromCart,
}) {
  const [showModal, setShowModal] = useState(false);
  const [selectedWine, setSelectedWine] = useState({});
  return (
    <div>
      <BannarText text="Be smart, just drink the best and fuck the rest!" />
      <section className="container mb-3">
        <div className="row wine-products justify-content-center pt-4">
          {wines.map((wine) => (
            <div
              key={wine._id}
              className="px-2 pb-2 col-12 col-sm-4 col-md-3 col-lg-2"
            >
              <Card
                className="border-0"
                style={{ cursor: "pointer" }}
                onClick={() => {
                  setSelectedWine(wine);
                  setShowModal(true);
                }}
              >
                <Card.Img
                  variant="top"
                  className="mx-auto"
                  src={wine.image}
                  style={{ maxWidth: "300px" }}
                />
                <p className="text-center">{wine.title}</p>
              </Card>
              <div className="d-flex justify-content-around my-1">
                {wine.qty === 0 && (
                  <OverlayTrigger
                    overlay={
                      <Tooltip id="tooltip-disabled">
                        {wine.inventory === 0 ? "Out of stock" : "Add to Cart"}
                      </Tooltip>
                    }
                  >
                    <button
                      type="button"
                      className="btn btn-outline-primary btn-block"
                      disabled={wine.inventory === 0 ? true : false}
                      onClick={() => addWineToCart(wine)}
                    >
                      <FontAwesomeIcon icon={faCartPlus} />{" "}
                      {Intl.NumberFormat("de-De", {
                        style: "currency",
                        currency: "EUR",
                      }).format(wine.price)}
                    </button>
                  </OverlayTrigger>
                )}
                {wine.qty > 0 && (
                  <>
                    <OverlayTrigger
                      overlay={
                        <Tooltip id="tooltip-disabled">Quantity</Tooltip>
                      }
                    >
                      <span className="d-inline-block">
                        <div
                          className="btn btn-outline-primary border-0 rounded-circle"
                          size="sm"
                        >
                          {wine.qty}
                        </div>
                      </span>
                    </OverlayTrigger>
                    <OverlayTrigger
                      overlay={
                        <Tooltip id="tooltip-disabled">
                          {wine.inventory === 0
                            ? "Last available item added to cart"
                            : "Increase Quantity"}
                        </Tooltip>
                      }
                    >
                      <span className="d-inline-block">
                        <button
                          className="btn btn-outline-primary rounded-circle"
                          size="sm"
                          disabled={wine.inventory === 0 ? true : false}
                          onClick={() => {
                            addWineToCart(wine);
                          }}
                        >
                          <FontAwesomeIcon icon={faPlus} />
                        </button>
                      </span>
                    </OverlayTrigger>
                    <OverlayTrigger
                      overlay={
                        <Tooltip id="tooltip-disabled">
                          Decrease Quantity
                        </Tooltip>
                      }
                    >
                      <span className="d-inline-block">
                        <button
                          className="btn btn-outline-primary rounded-circle"
                          size="sm"
                          onClick={() => {
                            removeWineFromCart(wine);
                          }}
                        >
                          <FontAwesomeIcon icon={faMinus} />
                        </button>
                      </span>
                    </OverlayTrigger>
                  </>
                )}
              </div>
            </div>
          ))}
        </div>
      </section>
      <Modal
        show={showModal}
        onHide={() => setShowModal(false)}
        centered
        size="xl"
        scrollable
      >
        <Modal.Header closeButton>
          <Modal.Title>
            {selectedWine.title}
            <div className="d-flex justify-content-around">
              {selectedWine.qty === 0 && (
                <OverlayTrigger
                  overlay={
                    <Tooltip id="tooltip-disabled">
                      {selectedWine.inventory === 0
                        ? "Out of stock"
                        : "Add to Cart"}
                    </Tooltip>
                  }
                >
                  <button
                    type="button"
                    className="btn btn-outline-primary btn-block"
                    disabled={selectedWine.inventory === 0 ? true : false}
                    onClick={() => addWineToCart(selectedWine)}
                  >
                    <FontAwesomeIcon icon={faCartPlus} />{" "}
                    {Intl.NumberFormat("de-De", {
                      style: "currency",
                      currency: "EUR",
                    }).format(selectedWine.price)}
                  </button>
                </OverlayTrigger>
              )}
              {selectedWine.qty > 0 && (
                <>
                  <OverlayTrigger
                    overlay={<Tooltip id="tooltip-disabled">Quantity</Tooltip>}
                  >
                    <span className="d-inline-block">
                      <div
                        className="btn btn-outline-primary border-0 rounded-circle"
                        size="sm"
                      >
                        {selectedWine.qty}
                      </div>
                    </span>
                  </OverlayTrigger>
                  <OverlayTrigger
                    overlay={
                      <Tooltip id="tooltip-disabled">
                        {selectedWine.inventory === 0
                          ? "Last available item added to cart"
                          : "Increase Quantity"}
                      </Tooltip>
                    }
                  >
                    <span className="d-inline-block">
                      <button
                        className="btn btn-outline-primary rounded-circle"
                        size="sm"
                        disabled={selectedWine.inventory === 0 ? true : false}
                        onClick={() => {
                          addWineToCart(selectedWine);
                        }}
                      >
                        <FontAwesomeIcon icon={faPlus} />
                      </button>
                    </span>
                  </OverlayTrigger>
                  <OverlayTrigger
                    overlay={
                      <Tooltip id="tooltip-disabled">Decrease Quantity</Tooltip>
                    }
                  >
                    <span className="d-inline-block">
                      <button
                        className="btn btn-outline-primary rounded-circle"
                        size="sm"
                        onClick={() => {
                          removeWineFromCart(selectedWine);
                        }}
                      >
                        <FontAwesomeIcon icon={faMinus} />
                      </button>
                    </span>
                  </OverlayTrigger>
                </>
              )}
            </div>
          </Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <div className="card">
            <div className="row no-gutters">
              <div className="col-md-4">
                <img
                  src={selectedWine.image}
                  className="card-img"
                  alt="Rice pitcher"
                />
              </div>
              <div className="col-md-8">
                <div className="card-body">
                  <p id="description" className="card-text">
                    {selectedWine.description}
                  </p>
                </div>
              </div>
            </div>
          </div>
        </Modal.Body>
        <Modal.Footer></Modal.Footer>
      </Modal>
    </div>
  );
}
